.button {
  margin: 10px;
  padding: 10px 15px;
  font-size: 20px;
  border: none;
  border-radius: 5px;
  transition: transform 0.15s, background-color 0.15s;
  font-family: 'Dancing Script', cursive;
}

.button:hover {
  background-color: rgb(226, 226, 146);
  /* transform: scale(1.05); */
}
